.ToolBar{
    height:56px;
    width:100%;
    position:fixed;
    top:0;
    left:0;
    background:#703b09;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack:justify;
        justify-content:space-between;
    -ms-flex-align:center;
        align-items:center;
    padding:0 20px;
    z-index:90;
}


.ToolBar nav{
    height:100%;
}


.sideDrawer{
    display:default;
}

@media (min-width:500px){
    .sideDrawer{
        display:none;
    }
}




